function sort(arr) {
  return arr.sort((a, b) => {
    if (a.name.toLocaleLowerCase() < b.name.toLocaleLowerCase()) {
      return -1;
    }
    if (a.name.toLocaleLowerCase() > b.name.toLocaleLowerCase()) {
      return 1;
    }

    return 0;
  })
}

function getContacts(filterBy = null) {
  var contactsToReturn = loadFromStorage(CONTACTS_DB) || contacts;
  return new Promise((resolve, reject) => {
    if (filterBy) {
      contactsToReturn = filter(filterBy)
    }
    saveToStorage(CONTACTS_DB, contactsToReturn)
    resolve(sort(contactsToReturn))
  })
}

function getContactById(id) {
  const contacts = loadFromStorage(CONTACTS_DB);
  return new Promise((resolve, reject) => {
    var contact = contacts.find(contact => contact._id === id)
    contact ? resolve(contact) : reject(`Contact id ${id} not found!`)
  })
}

function deleteContact(id) {
  return new Promise((resolve, reject) => {
    const index = contacts.findIndex(contact => contact._id === id)
    if (index !== -1) {
      contacts.splice(index, 1)
    }
    saveToStorage(CONTACTS_DB, contacts)
    resolve(contacts)
  })
}

function _updateContact(contact) {
  return new Promise((resolve, reject) => {
    const index = contacts.findIndex(c => contact._id === c._id)
    if (index !== -1) {
      contacts[index] = contact
    }
    saveToStorage(CONTACTS_DB, contacts)
    resolve(contact)
  })
}

function _addContact(contact) {
  return new Promise((resolve, reject) => {
    contact._id = _makeId()
    contact.imgUrl = `https://randomuser.me/api/portraits/med/men/${getRandomInt(1, 100)}.jpg`
    contacts.push(contact)
    saveToStorage(CONTACTS_DB, contacts)
    resolve(contact)
  })
}

function saveContact(contact) {
  if (contact._id) {
    saveToStorage(CONTACTS_DB, contacts)
    return _updateContact(contact)
  } else {
    saveToStorage(CONTACTS_DB, contacts)
    return _addContact(contact)
  }
}

function getEmptyContact() {
  return {
    name: '',
    email: '',
    phone: ''
  }
}

function filter(term) {
  term.name = term.name.toLocaleLowerCase()
  return contacts.filter(contact => {
    return contact.name.toLocaleLowerCase().includes(term.name) &&
      contact.phone.toLocaleLowerCase().includes(term.phone) ||
      contact.email.toLocaleLowerCase().includes(term.email)
  })
}



function _makeId(length = 10) {
  var txt = ''
  var possible = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789'
  for (var i = 0; i < length; i++) {
    txt += possible.charAt(Math.floor(Math.random() * possible.length))
  }
  return txt
}

function getRandomInt(min, max) {
  min = Math.ceil(min);
  max = Math.floor(max);
  return Math.floor(Math.random() * (max - min) + min); //The maximum is exclusive and the minimum is inclusive
}